<template>
    <div class=container>
        <div class="title">
            <img src="@/assets/content-title.png" alt="">
        </div>
        <div class="nav">
            <div class="item" v-for="item in list" :key="item.path">
                <span :class="{'act': $route.path.indexOf(item.path) === 0 && item.path !== ''}" @click="goFn(item.path)">{{item.name}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    // @ is an alias to /src

    export default {
        name: '',
        data() {
            return {
                list: []
            }
        },
        watch: {
            $route(to){
                this.routeFn(to.path)
            }
        },
        methods: {
            routeFn(path) {
                if (path.indexOf('caseTargetManagement') > 0) {
                    this.list = [
                        {name: '执法对象管理(自然人)', path: '/caseTargetManagement/personage'},
                        {name: '执法对象管理(企业)', path: '/caseTargetManagement/company'},
                    ]
                }
                if (path.indexOf('approval') > 0) {
                    this.list = [
                        {name: '行政审批', path: '/approval'},
                    ]
                }
                if (path.indexOf('registrationManagement') > 0) {
                    this.list = [
                        {name: '投诉登记', path: '/registrationManagement/complaintsRegistration'},
                        {name: '单位值班表', path: '/registrationManagement/dutyRoster'},
                        {name: '单位通信录', path: '/registrationManagement/addressBook'},
                    ]
                }
                if (path.indexOf('procedure') > 0) {
                    this.list = [
                        {name: '简易程序', path: '/procedure/simple'},
                        {name: '一般程序', path: ''},
                    ]
                }
                if (path.indexOf('lawData') > 0) {
                    this.list = [
                        {name: '法律法规', path: '/lawData'},
                    ]
                }
            },
            goFn(path) {
                if (path !== '') {
                    this.$router.push(path)
                }
            }
        },
        mounted() {
            this.routeFn(this.$route.path)

        }
    }
</script>

<style lang="scss" scoped>
    @import "~@/styles/theme.scss";
    .container {
    }
    .title {
        height: 80px;
        background-color: #006CFF;
        img {
            width: 100%;
            height: 100%;
        }
    }
    .nav {
        padding-top: 50px;
        font-size: 20px;
        .item {
            text-align: left;
            padding-left: 32px;
            cursor: pointer;
            margin-bottom: 40px;
            span {
                color: #fff;
            }
            .act {
                color: #2ACFF5;
                border-bottom: 4px solid #2ACFF5;
            }
        }
    }
</style>
