<template>
    <div class="indexHeader clearfix">
        <div class="title-zh">
            <img src="@/assets/home-logo.png" alt="">
        </div>
        <el-row :gutter="20" class="menu">
            <el-col :span="6" @click.native="mapFn"><svg-icon icon-class="ditu" /> 地图</el-col>
            <el-col :span="6"><el-badge :value="3" class="item notify"><svg-icon icon-class="tongzhi" /></el-badge> 通知</el-col>
            <el-col :span="6"><svg-icon icon-class="time" style="margin-top: 1px;"/> {{nowTime}}</el-col>
            <el-col :span="6"><svg-icon icon-class="my" style="margin-top: 1px;"/> XXX <a href="#">[退出]</a></el-col>
        </el-row>
    </div>
</template>

<script>
    // @ is an alias to /src
    export default {
        name: '',
        data() {
            return {
                nowTime: ''
            }
        },
        created: function() {
            this.timeFn();
        },
        methods: {
            timeFn: function() {
                this.nowTime = this.parseTime(new Date(), '{h}:{i}');
                setTimeout(() => {
                    this.timeFn();
                }, 1000);
            },
            parseTime(time, cFormat) {
                if (arguments.length === 0) {
                    return null
                }
                const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
                let date
                if (typeof time === 'object') {
                    date = time
                } else {
                    if (('' + time).length === 10) time = parseInt(time) * 1000
                    date = new Date(time)
                }
                const formatObj = {
                    y: date.getFullYear(),
                    m: date.getMonth() + 1,
                    d: date.getDate(),
                    h: date.getHours(),
                    i: date.getMinutes(),
                    s: date.getSeconds(),
                    a: date.getDay()
                }
                const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
                    let value = formatObj[key]
                    // Note: getDay() returns 0 on Sunday
                    if (key === 'a') { return ['日', '一', '二', '三', '四', '五', '六'][value ] }
                    if (result.length > 0 && value < 10) {
                    value = '0' + value
                    }
                    return value || 0
                })
                return time_str
            },
            mapFn() {
                this.$router.push('map')
            }
        }
    }
</script>

