<template>
    <div class="">
        <el-breadcrumb class="app-breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item v-for="(item, index) in levelList" :key="item.path" v-if="item.meta.title">
                <span class="item-no" v-if="item.redirect === 'noredirect'||index === levelList.length - 1">{{item.meta.title}}</span>
                <a class="item" v-else @click="handleLink(item)">{{item.meta.title}}</a>
            </el-breadcrumb-item>
        </el-breadcrumb>
    </div>
</template>

<script>
    // @ is an alias to /src

    export default {
        name: '',
        data() {
            return {
                levelList: null
            }
        },
        watch: {
            $route() {
                this.getBreadcrumb()
            }
        },
        methods: {
            getBreadcrumb() {
                console.log(this.$route.matched);
                let matched = this.$route.matched.filter(item => {
                    if (item.name) {
                        return true
                    }
                });
                matched = [{ path: '/', meta: { title: '首页' }}].concat(matched);
                this.levelList = matched;
                console.log(matched);
            },
            handleLink(item) {
                this.$router.push(item.path)
            }
        },
        created() {
            this.getBreadcrumb()
        }
    }
</script>

<style lang="scss" scoped>
    @import "~@/styles/theme.scss";
    .app-breadcrumb {
        color: #ffffff;
        .item {
            color: #ffffff;
            display: inline-block;
            /*line-height: 28px;*/
            height: 15px;
            line-height: 15px;
        }
        .item-no {
            color: #ccc;
            display: inline-block;
            height: 15px;
            line-height: 15px;
        }
    }
</style>
